'use strict';

// Get all the <span> elements that represent the headlines
const headlineSpans = document.querySelectorAll('.tree li > span');

// Add click event listeners to each headline span
headlineSpans.forEach(span => {
  span.addEventListener('click', (e) => {
    // Prevent the default behavior of the click event (e.g., following links)
    e.preventDefault();

    // Toggle the visibility of the next sibling (ul)
    const nestedList = span.parentElement.querySelector('ul');

    if (nestedList) {
      nestedList.classList.toggle('hidden');
    }
  });
});
